import { useEffect, useState } from "react";
// import { useNavigate } from "react-router-dom";
import readXlsxFile from "read-excel-file";
import * as XLSX from "xlsx";

import {
  ExcelContent,
  InputWrap,
  Input,
  FileInput,
  CheckBox,
  Table,
} from "./lib/styled";

const Excel = () => {
  const [excelFilterOne, setExcelFilterOne] = useState("");
  const [excelFilterTwo, setExcelFilterTwo] = useState("");
  const [excelFilterThree, setExcelFilterThree] = useState("");
  const [excelFile, setExcelFile] = useState([
    {
      intentName: "",
      intent: "",
    },
  ]);
  const [firstFilterArray, setFirstFilterArray] = useState([]);
  const [secondFilterArray, setSecondFilterArray] = useState([]);
  const [thirdFilterArray, setThirdFilterArray] = useState([]);
  const map = {
    intentName: "intentName",
    intent: "intent",
  };

  const [confirmCheck, setConfirmCheck] = useState(false);
  const [confirmIntentFilter, setConfirmIntentFilter] = useState("");
  const [confirmFilterArray, setConfirmFilterArray] = useState([]);

  const initializeData = () => {
    setExcelFilterOne("");
    setExcelFilterTwo("");
    setExcelFilterThree("");
    setConfirmIntentFilter("");
    setFirstFilterArray([]);
    setSecondFilterArray([]);
    setThirdFilterArray([]);
    setConfirmFilterArray([]);
    setConfirmCheck(false);
  };

  const xlsxToJson = async (targetFile) => {
    const rows = await readXlsxFile(targetFile, { map, sheet: 1 });
    setExcelFile(() => rows.rows);
  };

  const isSearchOne = excelFile.length && excelFilterOne.length > 0;
  const isSearchTwo = isSearchOne && excelFilterTwo.length > 0;
  const isSearchThree = isSearchTwo && excelFilterThree.length > 0;

  useEffect(() => {
    if (excelFilterOne || excelFilterTwo || excelFilterThree) {
      if (isSearchOne) {
        const filter = excelFile.filter((item) => {
          return item.intent.toString().includes(excelFilterOne);
        });
        setFirstFilterArray(() => filter);
      }

      if (isSearchTwo) {
        const filter = firstFilterArray.filter((item) => {
          return item.intent.toString().includes(excelFilterTwo);
        });
        setSecondFilterArray(() => filter);
      }

      if (isSearchThree) {
        const filter = secondFilterArray.filter((item) => {
          return item.intent.toString().includes(excelFilterThree);
        });
        setThirdFilterArray(() => filter);
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [excelFilterOne, excelFilterTwo, excelFilterThree]);

  useEffect(() => {
    if (confirmIntentFilter && confirmCheck) {
      const isLastSearchArray = isSearchThree
        ? thirdFilterArray
        : isSearchTwo
        ? secondFilterArray
        : isSearchOne
        ? firstFilterArray
        : excelFile;
      const filter = isLastSearchArray.filter((item) => {
        return item.intentName.toString().includes(confirmIntentFilter);
      });
      setConfirmFilterArray(() => filter);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [confirmIntentFilter]);

  // 필터링된 데이터를 엑셀 파일로 변환하는 함수
  const convertToExcel = (filteredData) => {
    const wb = XLSX.utils.book_new();
    const ws = XLSX.utils.json_to_sheet(filteredData);
    XLSX.utils.book_append_sheet(wb, ws, "Filtered Data");
    const excelBuffer = XLSX.write(wb, { bookType: "xlsx", type: "array" });
    saveAsExcel(excelBuffer, "filtered_data.xlsx");
  };

  // 변환된 엑셀 파일을 다운로드하는 함수
  const saveAsExcel = (buffer, filename) => {
    const blob = new Blob([buffer], { type: "application/octet-stream" });
    const url = URL.createObjectURL(blob);
    const link = document.createElement("a");
    link.href = url;
    link.setAttribute("download", filename);
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  return (
    <div className="Excel">
      <h1>Excel Filtering</h1>
      <div>
        엑셀파일 1행 열의 이름은 intentName, 2행의 열이름은 intent 이어야
        합니다.
      </div>
      <div>엑셀 파일 데이터가 2줄 이상일때 작동합니다.</div>
      <div>
        <FileInput
          type="file"
          accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
          onChange={(e) => {
            xlsxToJson(e.target.files[0]);
            initializeData();
          }}
        />
      </div>
      <ExcelContent>
        <InputWrap>
          <Input
            type="text"
            value={excelFilterOne}
            disabled={confirmCheck ? true : false}
            placeholder="1차 필터를 입력해주세요"
            onChange={(e) => {
              setExcelFilterOne(e.target.value);
            }}
          />
          <Input
            type="text"
            value={excelFilterTwo}
            disabled={confirmCheck ? true : excelFilterOne ? false : true}
            placeholder="2차 필터를 입력해주세요"
            onChange={(e) => {
              setExcelFilterTwo(e.target.value);
            }}
          />
          <Input
            type="text"
            value={excelFilterThree}
            disabled={confirmCheck ? true : excelFilterTwo ? false : true}
            placeholder="3차 필터를 입력해주세요"
            onChange={(e) => {
              setExcelFilterThree(e.target.value);
            }}
          />
        </InputWrap>
        <span>인텐트 이름 필터</span>
        <CheckBox
          type="checkbox"
          checked={confirmCheck}
          onChange={() => {
            confirmCheck ? setConfirmCheck(false) : setConfirmCheck(true);
          }}
        />
        <InputWrap>
          <Input
            type="text"
            disabled={confirmCheck ? false : true}
            value={confirmIntentFilter}
            onChange={(e) => {
              setConfirmIntentFilter(e.target.value);
            }}
          />
        </InputWrap>
        {excelFile.length > 1 ? (
          <button
            onClick={() => {
              convertToExcel(
                confirmIntentFilter.length > 0 && confirmCheck
                  ? confirmFilterArray
                  : isSearchThree
                  ? thirdFilterArray
                  : isSearchTwo
                  ? secondFilterArray
                  : isSearchOne
                  ? firstFilterArray
                  : excelFile
              );
            }}
          >
            필터링 결과 Excel 다운로드
          </button>
        ) : (
          <></>
        )}
      </ExcelContent>

      <Table>
        <thead>
          <tr>
            <th>인텐트 이름</th>
            <th>인텐트</th>
          </tr>
        </thead>
        <tbody>
          {(confirmIntentFilter.length > 0 && confirmCheck
            ? confirmFilterArray
            : isSearchThree
            ? thirdFilterArray
            : isSearchTwo
            ? secondFilterArray
            : isSearchOne
            ? firstFilterArray
            : excelFile
          ).map((item, index) => {
            return (
              <tr key={index}>
                <td>{item.intentName}</td>
                <td>{item.intent}</td>
              </tr>
            );
          })}
        </tbody>
      </Table>
    </div>
  );
};

export default Excel;
